import axios, { AxiosRequestConfig } from "axios";
import { BobjectType, SearchRequest } from "./types";

const BASE_URLS = {
  production: "https://bobject-api.bloobirds.com",
  staging: "https://bobject-api.staging-bloobirds.com",
  development: "https://bobject-api.dev-bloobirds.com",
  local: "http://localhost:8084"
};

console.log("Setting up the bobject api with baseURL", process.env.BOBJECT_API || BASE_URLS[process.env.ENV]);

const api = axios.create({
  baseURL: process.env.BOBJECT_API || BASE_URLS[process.env.ENV],
  withCredentials: true
} as AxiosRequestConfig);

api.interceptors.request.use(request => {
  if (localStorage.getItem("bb-app-session")) {
    const session = JSON.parse(localStorage.getItem("bb-app-session"));
    if (session?.token) {
      request.headers["Authorization"] = `Bearer ${session.token}`;
    }
    request.headers["X-Fingerprint"] = session?.hashedFingerprint;
  }
  return request;
});

export const postSearchSubscription = async (
  options: SearchRequest,
  bobjectType: BobjectType
) => {
  const accountId = sessionStorage.getItem("accountId");
  return api.post(`/${accountId}/${bobjectType}/search`, options);
};

export const postAggregationSubscription = async (
  options: SearchRequest,
  bobjectType: BobjectType
) => {
  const accountId = sessionStorage.getItem("accountId");
  return api.post(`/${accountId}/${bobjectType}/aggregation`, options);
};
