interface Mapping {
  prefix: string;
  port: number;
}

const mappings: Mapping[] = [
  { prefix: "/entities", port: 8080 },
  { prefix: "/utils", port: 8081 },
  { prefix: "/auth", port: 8082 },
  { prefix: "/scheduler", port: 8083 },
  { prefix: "/bobjects", port: 8084 },
  { prefix: "/calls", port: 8086 },
  { prefix: "/featureFlags", port: 8090 },
  { prefix: "/hubspot", port: 8091 },
  { prefix: "/statistics", port: 8092 },
  { prefix: "/integrations", port: 8093 },
  { prefix: "/linkedin", port: 8095 },
  { prefix: "/messaging", port: 8096 },
  { prefix: "/workflows", port: 8097 },
  { prefix: "/logging", port: 8070 },
  { prefix: "/copilot", port: 8500 },
];

const baseUrls = {
  production: "https://gateway.bloobirds.com",
  staging: "https://gateway.staging-bloobirds.com",
  development: "https://gateway.dev-bloobirds.com",
  local: "http://localhost:1234" // This fake url will be replaced later
};

// This is a temporary function to fake api gateway behaviour in local
export const getLocalUrl = (url: string) => {
  for (const { prefix, port } of mappings) {
    if (url.startsWith(prefix)) {
      return `http://localhost:${port}${
        url.startsWith("/") ? "" : "/"
      }${url.replace(prefix, "")}`;
    }
  }
  throw new Error(`URL mapping not found for "${url}"`);
};

export const getBaseUrl = (environment: string) => {
  return baseUrls[environment];
};
