import axios, { AxiosRequestConfig } from "axios";

const BASE_URLS = {
  production: "https://web-api.bloobirds.com",
  staging: "https://web-api.staging-bloobirds.com",
  development: "https://web-api.dev-bloobirds.com",
  local: "http://localhost:8081"
};

console.log("Setting up the web api with baseURL", process.env.WEB_API || BASE_URLS[process.env.ENV]);

const webApi = axios.create({
  baseURL: process.env.WEB_API || BASE_URLS[process.env.ENV || "local"],
  withCredentials: true
} as AxiosRequestConfig);

webApi.interceptors.request.use(request => {
  //TODO: I want a better way to handle the token
  if (localStorage.getItem("bb-app-session")) {
    const session = JSON.parse(localStorage.getItem("bb-app-session"));
    if (session?.token) {
      request.headers["Authorization"] = `Bearer ${session.token}`;
    }
    request.headers["X-Fingerprint"] = session?.hashedFingerprint;
  }
  request.headers["content-type"] = "application/json";
  return request;
});

export { webApi };
