import axios from "axios";
import { getBaseUrl, getLocalUrl } from "../utils/url";

interface CreateApiOptions {
  getToken: () => string | Promise<string>;
  getFingerprint?: () => string | Promise<string>;
  getEnvironment: () => string;
  getRefreshToken?: () => string | Promise<string>;
  updateCredentials?: (token: string, refreshToken: string, hashedFingerprint: string) => void;
}

export function createApi({
  getToken,
  getEnvironment,
  getFingerprint,
  getRefreshToken,
  updateCredentials,
}: CreateApiOptions) {
  const api = axios.create({
    baseURL: getBaseUrl(getEnvironment()),
    headers: {
      "content-type": "application/json"
    },
    withCredentials: true
  });

  api.interceptors.request.use(async request => {
    const environment = getEnvironment();
    if (environment === "local") {
      request.url = getLocalUrl(request.url);
    }

    if (getToken) {
      const token = await getToken();
      if (token) {
        request.headers["authorization"] = `Bearer ${token}`;
      }
    }

    if (getFingerprint) {
      const fingerprint = await getFingerprint();
      if (fingerprint) {
        request.headers["X-Fingerprint"] = fingerprint;
      }
    }

    if (getRefreshToken) {
      const refreshToken = await getRefreshToken();
      if (refreshToken) {
        request.headers["X-Refresh-Token"] = refreshToken;
      }
    }

    if (request.method.toLowerCase() === "get" && !request.data) {
      request.headers["content-type"] = "application/json";
      request.data = {};
    }

    return request;
  });

  api.interceptors.response.use(async response => {
    if (response.status === 401 && response.headers['X-Expired-JWT']) {
      const refreshToken = await getRefreshToken();
      if (refreshToken) {
        const { data, status } = await api.post("/auth/service/jwt/refresh", {
          refreshToken,
        });
        if (status === 200) {
          updateCredentials(data.token, data.refreshToken, data.hashedFingerprint);
        } else {
          updateCredentials(null, null, null);
        }
      }
    }

    return response;
  });

  return api;
}
