import { useGenericBobjectSubscription } from "./useGenericBobjectSubscription";
import { BobjectType, SearchRequest } from "../api/bobjects/types";

export const useAggregationSubscription = (
  request: SearchRequest,
  bobjectType: BobjectType,
  middlewares?: any[]
) => {
  return useGenericBobjectSubscription(
    request,
    bobjectType,
    {
      aggregation: true
    },
    middlewares
  );
};
