import { useWebSocket } from "react-use-websocket/dist/lib/use-websocket";
import { useEffect, useState } from "react";
import {
  atom,
  RecoilState,
  useRecoilCallback,
  useSetRecoilState
} from "recoil";
import { mutate } from "swr";
import { BobjectType } from "../api/bobjects/types";
import { debug } from "../utils/log";
import { eventsSubscriptionsFamily } from "./useEventSubscription";
import _ from "lodash";

enum ReadyState {
  UNINSTANTIATED = -1,
  CONNECTING = 0,
  OPEN = 1,
  CLOSING = 2,
  CLOSED = 3,
}

export interface WsMessage {
  action: string;
  bobjectType?: BobjectType;
  searchSubscriptionId?: string;
  _token?: string;
}

_.mixin({
  memoizeDebounce: function(func, wait=0, options={}) {
    // @ts-ignore
    var mem = _.memoize(function(param) {
      return _.debounce(func, wait, options)
    });
    return function(param: unknown){mem(param)(param)}
  }
});

export const connectionAtom: RecoilState<string> = atom({
  key: "ploverConnection",
  default: undefined
});

export const usePlover = (
  token: string,
  wsHost: string,
  debugFlag: boolean
) => {
  const setConnectionId = useSetRecoilState(connectionAtom);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  const { sendMessage, lastMessage, readyState, getWebSocket } = useWebSocket(
    wsHost,
    {
      shouldReconnect: () => true,
      onOpen: () => setIsAuthenticated(false)
    }
  );

  const triggerEventSubscription = useRecoilCallback(
    ({ snapshot }: { snapshot: any }) => {
      return async (id: string, message: any) => {
        const eventSubscriptions = await snapshot.getPromise(
            eventsSubscriptionsFamily(id)
        );
        if (debugFlag) {
          debug(`Notifying event subscriptions for id: ${id}`, eventSubscriptions);
        }
        eventSubscriptions.map((subscription: any) => {
          if (debugFlag) {
            debug(`Event launched for id ${id}`, subscription);
          }
          subscription.subscribedFunction(message)
        });
      };
    }
  );

  useEffect(() => {
    if (debugFlag) {
      debug(`WS State ${readyState}`);
      debug(`Authenticated: ${isAuthenticated}`);
    }
    if (readyState === ReadyState.OPEN && !isAuthenticated) {
      const loginMessage: WsMessage = {
        action: "login",
        _token: token
      };
      sendMessage(JSON.stringify(loginMessage));
      setIsAuthenticated(true);
      if (debugFlag) {
        debug(`Authenticating to websocket`);
      }
    }
    if (readyState === ReadyState.CONNECTING && debugFlag) {
      debug(`Opening the connection`);
    }
  }, [readyState, isAuthenticated]);

  useEffect(() => {
    if (getWebSocket() && debugFlag) {
      debug(`Incoming message at WS: ${getWebSocket().url}`);
    }
    if (lastMessage != null) {
      if (debugFlag) {
        debug("New message ", lastMessage);
      }
      const websocketMessage: WsMessage = JSON.parse(lastMessage.data);
      switch (websocketMessage.action) {
        case "login": {
          setConnectionId(websocketMessage["websocketConnectionId"]);
          break;
        }
        case "data": {
          triggerEventSubscription(
            `data-${websocketMessage.bobjectType}`,
            websocketMessage
          ).then(() => {
            if (debugFlag) {
              debug("Triggered event subscription for ", lastMessage);
            }
          });
          break;
        }
        case "searchSubscription": {
          mutate(websocketMessage.searchSubscriptionId)
            .then((e: any | undefined) => {
              if (debugFlag) {
                debug("Search mutation done", e);
              }
            })
            .catch((e: any | undefined) => {
              if (debugFlag) {
                debug("Search mutation failed", e);
              }
            });
          break;
        }
        default: {
          triggerEventSubscription(
            websocketMessage.action,
            websocketMessage
          ).then(() => {
            if (debugFlag) {
              debug("Triggered event subscription for ", lastMessage);
            }
          });
        }
      }
    }
  }, [lastMessage]);
};
