import { atomFamily, useSetRecoilState, useRecoilValue } from "recoil";
import hash from "object-hash";
import useSWR, {useSWRConfig} from "swr";
import {
  postAggregationSubscription,
  postSearchSubscription
} from "../api/bobjects/search";
import { useEffect, useState } from "react";
import { BobjectType, SearchRequest } from "../api/bobjects/types";
import { connectionAtom } from "./usePlover";
import { v4 as uuid } from "uuid";
import { deleteSubscription } from "../api/web/subscriptions/crud";

interface Options {
  aggregation?: boolean;
}

const instances = atomFamily({
  key: "instancesAtom",
  default: []
});

export const useGenericBobjectSubscription = (
  request: SearchRequest,
  bobjectType: BobjectType,
  options?: Options,
  middlewares?: any[]
) => {
  // Generate subscription if the query is not already a subscription
  const [instanceId] = useState(uuid());
  const { cache } = useSWRConfig()

  const websocketSubscriptionId: string = useRecoilValue(connectionAtom);
  const subscriptionId = `${hash({
    request,
    websocketSubscriptionId
  })}-${bobjectType}`;

  const setInstancesList = useSetRecoilState(instances(subscriptionId));

  const subscribedRequest: SearchRequest = {
    ...request,
    websocketSubscriptionId,
    subscriptionId
  };

  const result = useSWR(
    websocketSubscriptionId && request ? subscriptionId : null,
    async () =>
      options && options.aggregation
        ? postAggregationSubscription(subscribedRequest, bobjectType)
        : postSearchSubscription(subscribedRequest, bobjectType),
    { use: middlewares ? middlewares : [] }
  );

  useEffect(() => {
    if (websocketSubscriptionId) {
      setInstancesList(instanceList => [...instanceList, instanceId]);
    }
    return () => {
      setInstancesList(instanceList => {
        const filteredList = instanceList.filter(i => i != instanceId);
        if (filteredList.length <= 0 && websocketSubscriptionId) {
          deleteSubscription(subscriptionId, bobjectType).catch((e: any) =>
            console.error("Subscription failed to delete", e)
          );
          try {
            cache.delete(subscriptionId);
          } catch (e) {
            console.error("Failed to delete cache", e);
          }
        }
        return instanceList.filter(i => i != instanceId);
      });
      try {
        cache.delete(subscriptionId);
      } catch (e) {
        console.error("Failed to delete cache", e);
      }
    };
  }, [websocketSubscriptionId, subscriptionId]);

  return result;
};
