import { useEffect, useState } from "react";
import { v4 as uuid } from "uuid";
import {
  atomFamily,
  RecoilState,
  SerializableParam,
  SetterOrUpdater,
  useRecoilState
} from "recoil";
import { useDeepEffect } from "./useDeepEfect";

export interface Subscription {
  id: string;
  query?: object;
  subscribedFunction: CallbackFunction;
  subscriptionCollection: string;
  lastUpdate?: Date;
}

export interface EventSubscription extends Subscription {
  event: string;
}

export interface SubscriptionOptions {
  createSubscription: boolean;
}

export type CallbackFunction = Function;

export const eventsSubscriptionsFamily: (
  param: SerializableParam
) => RecoilState<EventSubscription[]> = atomFamily({
  key: "EventsSubscriptionsFamily",
  default: []
});

const generateSubscription = (
  subscriptionName: string,
  eventToListen: string,
  onNewData: CallbackFunction
) => {
  const subscriptionToConfigure: EventSubscription = {
    id: subscriptionName,
    lastUpdate: undefined,
    subscribedFunction: onNewData,
    subscriptionCollection: eventToListen,
    event: eventToListen
  };

  return subscriptionToConfigure;
};

const unmountSubscriptionFunction = (
  subscriptionName: string,
  removeSubscription: () => void
) => () => {
  if (subscriptionName) {
    removeSubscription();
  }
};

export const useEventSubscription = (
  eventToListen: string,
  onNewData: CallbackFunction,
  options: SubscriptionOptions = { createSubscription: true }
) => {
  const [subscriptionName, setSubscriptionName] = useState(uuid());

  const [subscriptions, setSubscriptions]: [
    EventSubscription[],
    SetterOrUpdater<EventSubscription[]>
  ] = useRecoilState(eventsSubscriptionsFamily(eventToListen));

  const removeSubscription = () => {
    setSubscriptions((prev: any[]) =>
      prev.filter(sub => sub.id !== subscriptionName)
    );
  };

  useEffect(
    () => unmountSubscriptionFunction(subscriptionName, removeSubscription),
    []
  );

  const activeSubscription = subscriptions.find(
    (sub: EventSubscription) => sub.id === subscriptionName
  );

  useDeepEffect(() => {
    if (options.createSubscription) {
      let nameToSubscribe = subscriptionName;
      if (activeSubscription) {
        unmountSubscriptionFunction(subscriptionName, removeSubscription)();
        nameToSubscribe = uuid();
        setSubscriptionName(nameToSubscribe);
      }
      const newSubscription = generateSubscription(
        nameToSubscribe,
        eventToListen,
        onNewData
      );
      setSubscriptions((prev: any) => [...prev, newSubscription]);
    }
  }, [eventToListen, options]);
};
